import Component from "jx/core/comps/Component";
export default class SimpleDOMView extends Component {
    constructor(dataObject) {
        super(Object.assign({
            render: "DOM",
            parentDOMView: $("body"),
            targetDOM: null,
            framerate: null,
            ratio: null
        }, dataObject));
    }
    pause() {
        if (super.pause()) {
        }
    }
    resume() {
        if (super.resume()) {
        }
    }
    close() {
        if (super.close()) {
        }
    }
    _initSync() {
        super._initSync();
        this.render = "DOM";
        this.ratio = this.dataObject.ratio || this.jx.config.app.ratio;
        this.framerate = this.dataObject.framerate || this.jx.config.app.framerate;
        this._enabled = true;
        this.children = [];
        this.view = this.dataObject.targetDOM || $(`<div id=${this.id}></div>`);
        // if (typeof window.orientation !== 'undefined') {
        //       var onOrientationChange =   ()=>{
        //             setTimeout((e)=>this.onResize(e), 100);
        //       }
        //       window.onorientationchange = onOrientationChange.bind(this);
        //       onOrientationChange.call(this);
        // } 
        // else {
        //     window.onresize = this.onResize.bind(this);
        //     this.onResize();
        // }
        this.dataObject.parentDOMView.append(this.view);
    }
    update() {
    }
    get enabled() {
        return this._enabled;
    }
    set enabled(value) {
        this._enabled = value;
    }
    set width(value) {
        this.view.width(value);
    }
    get width() {
        return 1024;
    }
    set height(value) {
        this.view.height(value);
    }
    get height() {
        return 768;
    }
    set visible(value) {
        this.view.css("display", value ? "initial" : "none");
    }
    addChild(child) {
        child = (child.isJXComponent) ? child : this.cc(child);
        if (child.view == undefined || child.render != "DOM") {
            console.warn("Could not Add child as it does not have a DOM view", child);
            return;
        }
        this.view.append(child.view);
        this.children.push(child);
        child.parent = this;
        this.onResize();
    }
    hasChild(child) {
        console.log(this.children, this.children.indexOf(child));
        return this.children.indexOf(child) != -1;
    }
    removeChild(child) {
        this.canvasHolder.remove(child.view);
        this.children.splice(this.children.indexOf(child), 1);
    }
    localToGlobal(...args) {
        console.log("not implemented");
        return null;
    }
    globalToLocal(...args) {
        console.log("not implemented");
        return null;
    }
    localToLocal(...args) {
        console.log("not implemented");
        return null;
    }
    onResize(event) {
    }
}
